import React from 'react'
import {BrowserRouter as Router,Route} from 'react-router-dom'


//返回内容
const RouteWithSubRoutes=(route)=>{
    return (
        <Route 
        exact = {route.exact}
        path={route.path} 
        render={props=>(
            <route.component {...props} routes={route.routes} />
        )}
        />
	);
}

export default RouteWithSubRoutes;