import React,{useEffect} from 'react'
import {BrowserRouter as Router,NavLink,Route,Redirect,useHistory,useLocation,useParams,useRouteMatch,withRouter, Link} from 'react-router-dom'
import './doc.css'
import SystemRouter from './RouteWithSubRoutes'
import {NavBar,Icon, Switch} from 'antd-mobile'
import RouteWithSubRoutes from './RouteWithSubRoutes'
import routes from './router.config'
import {PrivateRoute} from './App'
const DocContent = (props)=>{
    return <h1>{props.match.params.type}</h1>
}

const Doc = (props) => {
    return (
        <Router> 
            <div className='doc'>
                <div className="content">
                    {routes[1].routes.map((route, i) => (
                        <RouteWithSubRoutes key={i} {...route} />
                        
                    ))}
                    <Redirect from='/doc' to='/doc/core' />  
                </div>   
                <ol>
                    <li><NavLink to='/doc/core'>Core</NavLink></li>
                    <li><NavLink to='/doc/guide'>高级指引</NavLink></li>
                    <li><NavLink to='/doc/api'>API</NavLink></li>
                    <li><NavLink to='/doc/hooks'>Hooks</NavLink></li>
                </ol> 
                
            </div>
            
            
        </Router>
    )
}


export default withRouter( Doc )