import React from 'react'
import './home.css'
const Home = (props) => {
    console.log(props);
    return (
        <div style={{backgroundColor:'midnightblue'}}>
            <div style={{textAlign:'center'}} id='homes'>
                <h2>React</h2>
                <p>用于构建用户界面的 JavaScript 库</p>
                <div>
                    <button id='button1' onClick={
                        ()=>props.history.push('/doc')
                    }>快速开始</button>
                    <button id='button2' onClick={
                        ()=>props.history.push('/tuto')
                    }>入门教程&gt;</button>
                </div>
            </div>
        </div>
    )
}

export default Home